@import '../components/misc/grid_table';
@import "a_submenu/a_submenu";
@import "a_user_menu/a_user_menu";


///*************************************
//*******      ADMIN    ******
//**************************************/


header {
  position: relative;
  padding: 10px 0;
  height: 30px;
  z-index: 500;
  width: 100%;
  background: url(/pic/header-big.png);
  justify-content: flex-end;
  align-items: center;

  .header-tabs {
    position: absolute;
    bottom: 0;
    left: 100px;
    display: flex;

    div {
      height: 10px;
      padding: 10px;
      background-color: #fff;
      border-radius: 5px 5px 0 0;
      cursor: pointer; }

    div:first-child {
      left: 54px;
      background-color: #000;
      color: #ccc;
      margin-right: 10px; }

    div:last-child {
      border-bottom: 0;
      font-weight: 600; }

    a {
      height: 100%;
      width: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0; } } }


.breadcrumbs-adm {
  display: flex;
  margin: 25px 0;

  & > div {
    text-transform: uppercase; }

  div,
  a {
    padding: 3px 6px;
    font-size: 0.7rem;
    color: #7e98ba;
    font-weight: 600;
    margin-left: 5px; } }


/* *///////////////////////////////*/
/* *//////  PIC BOX  //////*/
/* *///////////////////////////////*/

.pic img {
  width: 100%;
  height: 100%;
  object-fit: contain; }

.pic .column {
  padding: 10px; }


.pic .column div {
  height: 150px;
  width: 150px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center; }


.pic span {
  padding: 20px 0px 5px 10px;
  max-width: 150px; }


/* *///////////////////////////////*/
/* */////////   DUMP  /////////*/
/* *///////////////////////////////*/

.dump-form * {
  padding: 10px;

  input {
    flex: 1; } }


///*//////////////////////////////*/
///*////////   Catalog  /////////*/
///*//////////////////////////////*/

.a-btn-action {
  background-color: #565d68;
  padding: 5px 10px;
  cursor: pointer;
  color: #fff;
  font-weight: 600; }

.tabs {

  & > .a-btn-action {
    margin-left: 60px; }

  .separator {
    background-color: #ebf4f4;
    margin: 10px 0;
    display: flex;
    justify-content: space-around;
    padding: 3px;
    color: #646464;
    position: sticky;
    bottom: 0; } }


.tabs {
  min-width: 320px;
  width: 100%;
  padding: 0px;
  margin: 0 auto 0 0;
  position: relative;

  & > section {
    display: none;
    background: #fff;
    border: 1px solid #ddd;

    & > .flex {
      margin: 0 0 5px;
      line-height: 1.5;
      color: #383838;
      animation-duration: .5s;
      animation-fill-mode: both;
      animation-name: fadeIn; } }

  & > input {
    display: none;
    position: absolute; }

  & > input:checked + label {
    color: #555;
    border-top: 1px solid #ffa500;
    border-bottom: 1px solid #fff;
    background: #fff; }

  & > label {
    display: inline-block;
    margin: 0 0 -1px;
    padding: 9px 3px;
    font-weight: 600;
    text-align: center;
    border: 1px solid #ddd;
    background: #f1f1f1;
    border-radius: 3px 3px 0 0;
    color: #8b8b8b;

    @media screen and (max-width: 400px) {
      padding: 15px; }

    @media screen and (max-width: 680px) {
      font-size: 7px; } }

  & > label:before {
    font-weight: normal;
    margin-right: 10px;
    @media screen and (max-width: 680px) {
      margin: 0;
      font-size: 18px; } }

  & > label:hover {
    color: #888;
    cursor: pointer; }

  #tab1:checked ~ #content-tab1,
  #tab2:checked ~ #content-tab2,
  #tab3:checked ~ #content-tab3,
  #tab4:checked ~ #content-tab4 {
    display: flex;
    flex-direction: column;
    padding: 15px;
    min-height: 100vh; }

  #content-tab1 {
    padding: 10px; }

  .row {
    padding: 5px;

    div, span, select, input {
      flex: 3;
      min-height: 18px;
      position: relative; }

    select {
      width: 100%; }

    strong {
      color: #4d4d4d;
      min-width: 120px;
      padding-right: 5px;
      display: flex;
      flex: 1;
      justify-content: flex-end;
      font-size: 14px;
      align-items: start;
      word-break: break-word; } } }

input[contenteditable],
span[contenteditable],
.category-properties select {
  border: 1px solid transparent;
  background-color: #fbfbfb; }


input[contenteditable]:hover,
span[contenteditable]:hover,
.category-properties select:hover {
  border-color: #ebf4f4;
  box-shadow: 0 0 2px 2px #f1f1f1;
  min-height: 18px; }


.categories {
  .row {
    justify-content: space-around;
    align-items: center; }

  .row:hover {
    background: #ccc; } }

/* *////////////////////////////////////*/
/* *///       INSTRUCTIONS        //////*/
/* *////////////////////////////////////*/
.doc {
  position: relative;

  ul {
    margin: 0 10px 0 0; } }

//module
.name {
  flex: 1; }


/* *////////////////////////////////////*/
/* *///       CATALOG PRODUCT        //////*/
/* *////////////////////////////////////*/

.edit:before {
  content: '\270e изменить';
  line-height: 54px;
  display: block;
  position: absolute;
  width: 80%;
  padding: 0 0 0 20%;
  height: 100%;
  color: #fff; }

.value {
  padding-left: 10px;

  &:focus {
    border-bottom: 1px #ddd solid; } }

.props {
  display: flex;
  flex-flow: column;
  margin: 20px 20px;

  .val {
    padding: 10px;
    flex: 1; } }

td a {
  border: 1px solid #949494;
  padding: 3px 8px; }


/* *///////////////////////////////*/
/* */////////   TABLE USERS  /////////*/
/* *///////////////////////////////*/

.btnadd-user {
  width: 205px;
  height: 49px; }

.buttons {
  display: inline-block;
  white-space: nowrap;
  text-align: center;
  line-height: 20px;
  background: #eeeeee;
  background: linear-gradient(top, #eeeeee 0%, #eeeeee 100%);
  border: 1px solid #a1a1a1;
  color: #333;
  text-shadow: 0 1px 0 rgba(255, 255, 255, .8);
  border-radius: .2em;
  box-shadow: 0 0 1px 1px rgba(255, 255, 255, .8) inset, 0 1px 0 rgba(0, 0, 0, .3);

  &:hover {
    background: #ffffff;
    background: linear-gradient(top, #ffffff 0%, #e5e5e5 100%); }

  &:active {
    box-shadow: 0 0 4px 2px rgba(0, 0, 0, .3) inset;
    position: relative;
    top: 1px; } }

.type {
  width: 200px; }

.edit {
  width: 90px; }

.phone {
  width: 105px; }

.filter {
  padding: 30px; }



/* *///////////////////////////////*/
/* */////////   COMMON   /////////*/
/* *///////////////////////////////*/

body {
  overflow-y: scroll;
  margin: 0;
  font-family: 'Roboto', sans-serif; }


a,
a:hover {
  text-decoration: none;
  color: #949494; }


ul, li {
  list-style: none;
  margin: 0;
  padding: 0; }


input,
textarea,
textarea:focus,
.button:focus {
  outline: none; }

.list,
.a label,
.button,
.buttons,
.del-prop,
.add-prop, {
  cursor: pointer; }

h2 {
  margin: 0; }

input {
  border: none;
  border-bottom: 1px solid #ccc;
  border-right: 1px solid #ccc; }


.wrap-admin {
  background-color: #fffF;
  margin: 0 5px; }


.adm-content {
  flex: 1;
  background-color: #f7f7f7;
  margin: 5px; }


.add-prop {
  background: #123aff;
  color: #fff;
  height: 100%;
  font-weight: 600;
  padding: 3px 6px;
  transition: .1s; }


.add-prop:hover {
  background: #0070ff;
  color: #fff; }

.add-prop-val {
  height: 17px;
  transition: .1s; }

.del-prop {
  width: 15px;
  height: 15px;
  position: absolute;
  background: red;
  right: 0;
  top: 100%;
  color: #fff;
  font-size: 8px;
  transition: .3s;
  border-radius: 0 0 7px 7px;
  line-height: 200%;

  &:hover {
    width: 50px;
    transition: .3s; }

  span {
    position: absolute; }

  span:nth-child(1) {
    opacity: 1;
    right: 5px; }

  span:nth-child(2) {
    opacity: 0;
    display: none; }

  &:hover.del-prop span:nth-child(1) {
    opacity: 0;
    transition: .3s; }

  &:hover.del-prop span:nth-child(2) {
    opacity: 1;
    transition: .3s;
    right: 6px;
    display: block; } }


.category-properties {
  margin: 5px 0 5px 50px; }

.wrap .list:hover {
  background: #f6f6f6; }

.properties {
  padding: 5px;

  &.column > select {
    margin: 5px 0 5px 50px; }

  select {
    margin: 5px 0; } }

.cat-prop {
  padding: 10px 0;
  font-weight: 600; }

.add-property {
  justify-content: flex-end;

  span {
    padding: 10px 10px 10px 0; } }


/* *///////////////////////*/
/* *///////  END  MENU  ////////*/
/* *///////////////////////*/

.confirm {
  text-align: center;
  width: 20px; }


